import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { ServiseComponent } from './servise/servise.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { WebComponent } from './web.component';






const childRoutes: Routes = [
  { path: '', component:HomeComponent , data: { titulo: 'UE Arturo Borja' } },
  { path: 'service', component: ServiseComponent, data: { titulo: 'Servicios' }},
  { path: 'contact-us', component: ContactUsComponent, data: { titulo: 'Contactanos' }},
  { path: 'home', component: HomeComponent, data: { titulo: 'Inicio' }},
]




@NgModule({
  imports: [ RouterModule.forChild(childRoutes) ],
  exports: [ RouterModule ]
})
export class ChildRoutesModule { }
